import React, { Component } from 'react';
import Login from './Login';
import Signup from './Signup';
import fakeAuth from './fakeAuth';
import Dashboard from './Dashboard';
import PageNotFound from './PageNotFound';
import Footer from './Footer';
import {
  BrowserRouter as Router,
  Redirect,
  Route,
  Switch
} from 'react-router-dom';


const PrivateRoute = ({ component: Component, ...rest }) => (
    <Route {...rest} render={props => (
    fakeAuth.isAuthenticated ? (
      <Component {...props}/>
    ) : (
      <Redirect to={{
        pathname: '/',
        state: { from: props.location }
      }}/>
    )
  )}/>
)


class App extends Component {

    constructor(props){
        super(props);
        console.log(props.location);


    }

    componentWillMount() {
        //const { match, location, history } = this.props        
        //console.log("App : " + location.pathname);
        /* if(location.pathname == '/') {
          console.log('sign-in.css')
            require('./css/sign-in-fix171230.css');
         }*/
      }

    render(){

        return (
            <Router >
            <div className="layout ">
            
            
            <Switch>
                <Route exact path="/" component={Login} />
                <Route path="/signup/:step?" component={Signup} />
                <Route path="/findid" component={FindId} />
                <Route path="/findpasswd" component={FindPassword} />
                <PrivateRoute path="/dashboard/:screen?/:detail?" component={Dashboard} />
                <Route component={PageNotFound} />
            </Switch>
                <Footer />
            
            
            
            </div>
            </Router> 
        );
    }
}

const FindId = () => (
<main className="main-container">
    <div className="main-wrap">
        <section className="section-container sign-in-form">
          <div>
                <h2>Find ID</h2>
          </div>
        </section>
    </div>
</main>

)
const FindPassword = () => (
<main className="main-container">
    <div className="main-wrap">
        <section className="section-container sign-in-form">
          <div>
            <h2>Find Password</h2>
          </div>
        </section>
    </div>
</main>
)


export default App;


