class ProductForm extends Component{
   constructor(props){
      super(props);
      
      this.state = {
          displayErrors: false,
          value:''};

      this.handleChange = this.handleChange.bind(this);
      this.handleSubmit = this.handleSubmit.bind(this);
   }

   handleSubmit(event) {
      if (event.target.checkValidity()===false) {
         // form is invalid! so we do nothing
         
         this.setState({ displayErrors: true });
         
         event.preventDefault();
         event.stopPropagation();
         return;
     }
     this.setState({ displayErrors: false });      
   }

   handleChange(event) {

      const target = event.target;
      const value = target.type === 'checkbox' ? target.checked : target.value;
      const name = target.name;

      if (name === "idSaved" ) {
          
      }
      this.setState({
        [name]: value
      });
  }

   render(){
      return(
         <form onSubmit={this.handleSubmit} className={this.state.displayErrors ? 'displayErrors' : ''} id="needs-validation" noValidate >
         <div class="form-group row sc--table-row">
         <div class="col-3 sc--table-col col-title">
              <span class="mark--required">*</span>
         </div>
         <div class="col-9 sc--table-col">
             <div class="form-check form-check-inline">
                 <label class="form-check-label">
                     <input class="form-check-input" type="radio" name="gridRadios" id="gridRadios1" value="option1" checked onChange={this.handleChange} />
                     
                 </label>
             </div>
             <div class="form-check form-check-inline">
                 <label class="form-check-label">
                     <input class="form-check-input" type="radio" name="gridRadios" id="gridRadios2" value="option2" onChange={this.handleChange} />
                     
                 </label>
             </div>

             <input class="form-control" type="text" id="unique-number" name="productNumber" placeholder=" ." onChange={this.handleChange} required />
             <div className="invalid-feedback">
             
             </div>
         </div>
     </div>

     <div class="form-group row sc--table-row">
         <div class="col-3 sc--table-col col-title">
              <span class="mark--required">*</span>
         </div>
         <div class="col-9 sc--table-col">
             <input class="form-control" type="text" id="product-name" name="productName"  placeholder=" ." onChange={this.handleChange} required />
             <div className="invalid-feedback">
             
             </div>
         </div>
     </div>

     <div class="form-group row sc--table-row">
         <div class="col-3 sc--table-col col-title">
             
         </div>
         <div class="col-9 sc--table-col">
             <input class="form-control" type="text" id="company" name="companyName"  placeholder=" ." onChange={this.handleChange} />
         </div>
     </div>

     <div class="button-group only-right">
         <div class="left">
         </div>
         <div class="center">
         </div>
         <div class="right">
             <button type="button" class="btn btn-primary btn-cancel"></button>
             <button type="submit" class="btn btn-primary btn-confirm"></button>
         </div>
     </div>

         </form>
      );

   }
}

class Product extends Component {
	render(){
		return(
         <section className="section-container">
         <ContentHeader />
         <div class="section-top-container">
            <h1 class="st--title">
             
            </h1>
            <h4 class="st--description">
              ITPO         .
            </h4>
         </div>
         <div class="section-content-container">
         <div class="section-content-wrap">
             <div class="container sc--table">
             <ProductForm />
             </div>
         </div>
         </div>
         </section>	
      );
	}
}
export default Product;


