import React, { Component } from 'react';
import {
    Link,
    withRouter
  } from 'react-router-dom';
 import PropTypes from 'prop-types';
 import iconmenugraph from './imgs/common/icon--menu--graph.svg';
 import iconmenupresent from './imgs/common/icon--menu--present.svg';
 import iconmenubag from './imgs/common/icon--menu--bag.svg';
 import iconmenulist from './imgs/common/icon--menu--list.svg';
 import iconmenuadd from './imgs/common/icon--menu--add.svg';
 import iconmenustore from './imgs/common/icon--menu--store.svg';
 import iconmenucalculator from './imgs/common/icon--menu--calculate.svg';
 import iconmenunote from './imgs/common/icon--menu--note.svg';
 import iconmenuuser from './imgs/common/icon--menu--user.svg';
 import iconmenucloser from './imgs/common/icon--menu--sidebar-closer.svg';
 import iconmenuopener from './imgs/common/icon--menu--sidebar-opener.svg';
 import whitelogo from './imgs/common/logo--mark--white.svg';
 import typologo from './imgs/common/logo--typo--mix-light-gray-white.svg';
 import sidebaractivator from './imgs/common/icon--menu--sidebar-activator--white.svg';
 import sidebardeactivator from './imgs/common/icon--menu--sidebar-deactivator--white.svg';

class Sidebar extends Component {

    constructor(props){
        super(props);
        
        this.state = {isToggleOn: true};
        this.handleClick = this.handleClick.bind(this);

    }

    handleClick(e){
        e.preventDefault();
        console.log('This link was clicked.');
        this.setState(prevState => ({
            isToggleOn: !prevState.isToggleOn
        }));
        console.log(this.state.isToggleOn);
        this.props.callbackFromParent(!this.state.isToggleOn);
    }
    
    render(){
        console.log(this.props.activated);
        const screen = this.props.activated;
        return(
            <aside className="side-bar-container">
            <div className="sb--header-wrap">
                <div className="sb--logo-front">
                    <img className="icon" src={whitelogo} />
                </div>

                <div className="sb--logo-back">
                    <img src={typologo} />
                </div>

                <button type="button" className="mobile-nav-activator" onClick={this.handleClick}>
                    <img className="sidebar-activator" src={sidebaractivator} />
                </button>
                <button type="button" className="mobile-nav-deactivator" onClick={this.handleClick}>
                    <img className="sidebar-deactivator" src={sidebardeactivator} />
                </button>
            </div>

            <div className="sb--menu-wrap">
                <ul className="menu-depth-wrap depth1">
                    <li className={(screen=="main")?"menu-item activate":"menu-item"}>
                        <Link to="/dashboard/main" >
                            <img className="mi--icon" src={iconmenugraph} />
                            <div className="mi--name"></div>
                        </Link>
                    </li>
                    <li className={(screen=="benefit")?("menu-item activate"):("menu-item")}>
                        <Link to="/dashboard/benefit" >
                            <img className="mi--icon" src={iconmenupresent} />
                            <div className="mi--name"></div>
                        </Link>
                        <ul class="menu-depth-wrap depth2">
                            <li class="menu-item activated">
                                <Link to="/dashboard/benefit/register">
                                    <img class="mi--icon" src={iconmenulist} />
                                    <div class="mi--name"></div>
                                </Link>
                            </li>
                            <li class="menu-item">
                                <Link to="/dashboard/benefit/result">
                                    <img class="mi--icon" src={iconmenuadd} />
                                    <div class="mi--name"></div>
                                </Link>
                            </li>
                        </ul>                        
                    </li>
                    <li className={(screen=="product")?("menu-item activate"):("menu-item")}>
                        <Link to="/dashboard/product" >
                            <img className="mi--icon" src={iconmenubag} />
                            <div className="mi--name"></div>
                        </Link>

                        <ul className="menu-depth-wrap depth2">
                            <li className="menu-item activated">
                                <Link to="/dashboard/product/list" >
                                    <img className="mi--icon" src={iconmenulist} />
                                    <div className="mi--name"></div>
                                </Link>
                            </li>
                            <li className="menu-item">
                                <Link to="/dashboard/product/register" >
                                    <img className="mi--icon" src={iconmenuadd} />
                                    <div className="mi--name"></div>
                                </Link>
                            </li>
                        </ul>
                    </li>
                    <li className={(screen=="store")?("menu-item activate"):("menu-item")}>
                        <Link to="/dashboard/store" >
                            <img className="mi--icon" src={iconmenustore} />
                            <div className="mi--name"> </div>
                        </Link>
                    </li>
                    <li className={(screen=="settlement")?("menu-item activate"):("menu-item")}>
                        <Link to="/dashboard/settlement" >
                            <img className="mi--icon" src={iconmenucalculator} />
                            <div className="mi--name"> </div>
                        </Link>
                    </li>
                    <li className={(screen=="review")?("menu-item activate"):("menu-item")} >   
                        <Link to="/dashboard/review" >
                            <img className="mi--icon" src={iconmenunote} />
                            <div className="mi--name"> </div>
                        </Link>
                    </li>
                    <li className={(screen=="membership")?("menu-item activate"):("menu-item")}>
                        <Link to="/dashboard/membership" >
                            <img className="mi--icon" src={iconmenuuser} />
                            <div className="mi--name"> </div>
                        </Link>
                    </li>
                    <li className="menu-item sidebar-controller">
                        <button type="button" className="closer" onClick={this.handleClick}>
                            <img className="mi--icon" src={iconmenucloser} />
                            <div className="mi--name">  </div>
                        </button>
                        <button type="button" className="opener" onClick={this.handleClick}>
                            <img className="mi--icon" src={iconmenuopener} />
                            <div className="mi--name">  </div>
                        </button>
                    </li>
                </ul>
            </div>
        </aside>)
    }
}

export default withRouter(Sidebar);


