
import React, { Component } from 'react';
import iconPageNotFound from './imgs/common/icon--page-not-found.svg';
import ContentHeader from './ContentHeader';
import MainSidebar from './MainSidebar';
class PageNotFound extends Component {
	
	render(){
		return(
			<main className="main-container">
			<div className="main-wrap sign-forms">
			<MainSidebar />
			<section className="section-container page-not-found-container">
         <ContentHeader />
         <div class="container pnf-wrap">
                        <div class="pnf--header">
                            <div class="head-icon">
                                <img class="icon" src={iconPageNotFound} />
                            </div>

                            <h1 class="title-label">
                                    .
                            </h1>

                            <p class="description-label">
                                   ,          .
                            </p>

                            <p class="help-label">
                                       . <br/>
                                  <a href="#">support@codelink.co.kr</a>    . <br/>
                                .                                    
                            </p>
                        </div>
                    </div>
	                    
	      </section>
			</div>
			</main>
			
		);
	}
}

export default PageNotFound;


