import React, { Component } from 'react';
import {
   Redirect,
   Link,
   withRouter
} from 'react-router-dom';
import fakeAuth from './fakeAuth';
import moment from 'moment';

class PopupContainer extends Component {

   constructor(props){
      super(props);
      this.state = {
         redirect : false,
         auth : null,
      }
      this.handleClick = this.handleClick.bind(this);
   }

   componentWillMount() {
      
      this.setState({auth:this.props.auth});

  }


   handleClick(e){
      e.preventDefault();
      fakeAuth.signout();
      this.setState({redirect:true});
  }  

   render(){
      const { auth, redirect } = this.state;
      let access_date = new Date(auth['principal']['recentAccessAt']);
      if(redirect){
         return(
             <Redirect to='/dashboard' />
         );

      }
      else {
         return(
            <div class="nc--ui--popup-container popup-container">
            <div class="pc-wrap">
                <div class="pc--header">
                    
                </div>
                <div class="pc--content">
                    <div class="pc--user-photo">
                    </div>
                    <div class="pc--user-name">
                     {auth['principal']['managerName']}
                    </div>
                    <div class="pc--user-email">
                     {auth['principal']['managerEmail']}
                    </div>
                    <div class="pc--user-connection key-value">
                        <div class="key"> </div>
                        <div class="value">{moment(access_date).format('YYYY.MM.DD HH:mm')}</div>
   
                        <div class="key"> (IP)</div>
                        <div class="value">{auth['details']['remoteAddress']}</div>
                    </div>
                </div>
                <div class="pc--footer">
                    <div class="button-group only-center">
                        <div class="left">
                        </div>
                        <div class="center">
                            <button type="button" class="btn btn-secondary btn-list" onClick={this.handleClick}></button>
                        </div>
                        <div class="right">
                        </div>
                    </div>
                </div>
            </div>
        </div>        
         );
   
      }

   }

}

export default PopupContainer;


