import React, { Component } from 'react';
import {
    Link,
    withRouter
  } from 'react-router-dom';
 import PropTypes from 'prop-types';
 import whitelogo from './imgs/common/logo--mark--white.svg';
 import typologo from './imgs/common/logo--typo--mix-light-gray-white.svg';
 import sidebaractivator from './imgs/common/icon--menu--sidebar-activator--white.svg';
 import sidebardeactivator from './imgs/common/icon--menu--sidebar-deactivator--white.svg';

class MainSidebar extends Component {

    static propTypes = {
        match: PropTypes.object.isRequired,
        location: PropTypes.object.isRequired,
        history: PropTypes.object.isRequired
    }


    
    render(){
            return(
                <aside className="side-bar-container">
                    <div className="sb--header-wrap">
                        <div className="sb--logo-front">
                            <img className="icon" src={whitelogo} />
                        </div>

                        <div className="sb--logo-back">
                            <img src={typologo} />
                        </div>

                        <button type="button" class="mobile-nav-activator">
                            <img className="sidebar-activator" src={sidebaractivator} />
                        </button>
                        <button type="button" class="mobile-nav-deactivator">
                            <img className="sidebar-deactivator" src={sidebardeactivator} />
                        </button>
                    </div>
                </aside>
            );
    }
}

export default withRouter(MainSidebar);


