
import React, { Component } from 'react';
import arrowright from './imgs/common/icon--arrow-right--light-gray.svg';
import ContentHeader from './ContentHeader';
import MainSidebar from './MainSidebar';
class Signup extends Component {
	
	render(){
		return(
			<main className="main-container">
			<div className="main-wrap sign-forms">
			<MainSidebar />
			<section className="section-container sign-up-form sign-up-agreement">
			
					
					<ContentHeader />
	                    <div className="sign-up-container">
	                        <div className="container su--header">
	                            <div className="step-current">
	                                <h1></h1>
	                            </div>

	                            <ul className="list-group list-group-horizontal list-group-decimal step-flow">
	                                <li className="list-group-item active">1.</li>
	                                <li className="list-group-item spliter"><img className="icon" src={arrowright} alt="arrow right" /></li>
	                                <li className="list-group-item">2.</li>
	                                <li className="list-group-item spliter"><img className="icon" src={arrowright} alt="arrow right" /></li>
	                                <li className="list-group-item">3. </li>
	                            </ul>
	                        </div>

	                        <div className="container su--agreement-form">
	                            <div className="su--af--wrap col-8">
	                                <div className="form-group row su--af--confirmation-all-agreement">
	                                    <div className="col-9 checkbox checkbox-group">
	                                        <div className="form-check form-check-inline">
	                                            <label className="form-check-label">
	                                                <input className="form-check-input" type="checkbox" />
	                                                <span className="text"> ,       .</span>
	                                            </label>
	                                        </div>
	                                    </div>
	                                    <div className="col-3 button-group">
	                                        <button type="button" className="btn btn-secondary btn-sign-up">
	                                             
	                                        </button>
	                                    </div>
	                                </div>

	                                <div className="form-grop row su--af--agreement-item">
	                                    <label className="header-title" htmlFor="agreement--service-policy"> </label>
	                                    <textarea className="form-control agreement-document" id="agreement--service-policy" rows="6"></textarea>

	                                    

	                                    <label className="form-check-label">
	                                        <input className="form-check-input" type="checkbox" />
	                                        <span className="text">   .</span>
	                                    </label>
	                                </div>

	                                <div className="form-grop row su--af--agreement-item">
	                                    <label className="header-title" htmlFor="agreement--service-policy">   ()</label>
	                                    <textarea className="form-control agreement-document" id="agreement--service-policy" rows="6"></textarea>

	                                    <label className="form-check-label">
	                                        <input className="form-check-input" type="checkbox" />
	                                        <span className="text">     .</span>
	                                    </label>
	                                </div>

	                                <div className="button-group only-center button-group-action">
	                                    <div className="left">
	                                    </div>
	                                    <div className="center">
	                                        <button type="button" className="btn btn-primary btn-cancel"></button>
	                                        <button type="button" className="btn btn-primary btn-confirm"></button>
	                                    </div>
	                                    <div className="right">
	                                    </div>
	                                </div>

	                                <div className="help-group">
	                                    <p>
	                                            02-OOOO-OOOO  <a href="#">help@codelink.co.kr</a>  . CODELINK    .
	                                    </p>
	                                </div>
	                            </div>
	                        </div>
	                    </div>
	                </section>
				</div>
			</main>
			
		);
	}
}

export default Signup;


