import React, { Component } from 'react';
import { DateRange } from 'react-date-range-kr';
import iconcalendar from './imgs/common/icon--calendar.svg';

class DateRangePicker extends Component {
	constructor(props){
		super(props);

		this.state = {showCalendar:false}
		this.onclick = this.onclick.bind(this);
	}
	onclick(e){
		this.setState({showCalendar:true});
	}
	handleSelect(range){
		console.log(range);
		// An object with two keys,
		// 'startDate' and 'endDate' which are Momentjs objects.
	}

	render(){
		const showCalendar = this.state.showCalendar;
		return (
			<section className="section-container">
			<div class="section-content-container">
			<div class="flex">
			<div class="initial ipt-date ipt-date-start">
				 <input class="form-control" type="text" value="2018/01/08" id="sales-management-date-start"  placeholder="" onClick={this.onclick}/>
			</div>
			<div class="initial">
				 <button type="button" class="btn btn-calendar">
					  <img class="icon" src={iconcalendar} />
				 </button>
			</div>
			<div className="container sc--table pl-0 pr-0">
			
			{showCalendar && <DateRange
				onInit={this.handleSelect}
				onChange={this.handleSelect}
				calendars="1"
				format="YYYY.MM.DD"
				lang={"ko"}
			/>}
			
			</div>		
	  		</div>
			
			
			</div>
			</section>
		)
	}
}

export default DateRangePicker;


