import React, { Component } from 'react';

import './Login.css';

import userblueicon from './imgs/common/icon--user-blue.svg';
import loginicon from './imgs/common/icon--login.svg';
import logotextonly from './imgs/common/logo--typo-only-codelink.svg';
import {
    Redirect,
    Link,
    withRouter
} from 'react-router-dom';
import fakeAuth from './fakeAuth';
import ContentHeader from './ContentHeader';
import MainSidebar from './MainSidebar';

class LoginForm extends Component {

    constructor(props){
        super(props);
        
        console.log(fakeAuth.isAuthenticated);
        this.state = {
            redirect: false,
            displayErrors: false,
            isSave : null,
            value:''};

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    componentWillMount(props){
        const checked = localStorage.getItem('isSave');
        this.setState({isSave:checked});
        
    }


    

    handleSubmit(event){
        
        if (event.target.checkValidity()===false) {
            // form is invalid! so we do nothing
            
            this.setState({ displayErrors: true });
            
            event.preventDefault();
            event.stopPropagation();
            return;
        }
        this.setState({ displayErrors: false });

        //alert('login user id : ' + this.state.user);
        //alert('login user pwd : ' + this.state.password);
        let url = 'http://13.125.50.147:8088/user';
        let headers = new Headers();

        headers.append('Authorization', 'Basic ' + btoa(this.state.user + ":" + this.state.password))

        fetch(url, {method:'GET',
                headers: headers,
                //credentials: 'user:passwd'
               })
        .then(response => response.json())   
        .then((json) => {
            console.log(json);
            if(json.authenticated !== undefined){

                fakeAuth.authenticate(json);
                this.setState({ redirect: true });       
                console.log(fakeAuth.isAuthenticated);
            }
            else{
                alert(json.message)
            }
            
        })
        .catch((error)=> {
            this.setState({ redirect: false });
            console.log(error);
        });  
        
        event.preventDefault();
    }
    
    handleChange(event) {

        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;

        if (name === "idSaved" ) {
            if(target.checked) localStorage.setItem('isSave', 'yes');
            //else sessionStorage.removeItem('isSave');
            else localStorage.setItem('isSave', 'no');
        }
        this.setState({
          [name]: value
        });
    }

    render(){
        const { redirect } = this.state;
        console.log(redirect);
        if(redirect){
            return(
                <Redirect to='/dashboard/main' />
            );

        }
        else {
          return (
            <form onSubmit={this.handleSubmit} className={this.state.displayErrors ? 'displayErrors' : ''} id="needs-validation" noValidate >
                <div className="form-group row">
                <div className="col-12 input input-id">
                    <input className="form-control" type="text" id="membership-id" name="user" placeholder=" " onChange={this.handleChange} required />
                    <div className="invalid-feedback">
                    
                    </div>
                </div>
                <div className="col-12 input input-password">
                    <input className="form-control" type="password" id="membership-password"  name="password" placeholder=" " onChange={this.handleChange}  required />
                    <div className="invalid-feedback">
                    
                    </div>
                </div>
                <div className="col-6 checkbox">
                    <div className="form-check form-check-inline">
                        <label className="form-check-label">
                            <input className="form-check-input" name="idSaved" type="checkbox" defaultChecked={this.state.isSave === 'yes'? true: false} onChange={this.handleChange}  /> 
                             
                        </label>
                    </div>
                </div>
                <div className="col-6 checkbox">
                    <div className="form-check form-check-inline">
                        <label className="form-check-label">
                            <input className="form-check-input" type="checkbox" onChange={this.handleChange} />
                             
                        </label>
                    </div>
                </div>
                <div className="col-12 button-group">
                    <button type="submit" className="btn btn-primary btn-login">
                         
                        <img className="icon" src={loginicon} alt="login" />
                    </button>
                </div>
                </div>
            </form>
        );  
        }


        
    }
}

class Login extends Component {

  render() {
    return (
        
        <main className="main-container">
        <div className="main-wrap sign-forms">
        <MainSidebar />
        <section className="section-container sign-in-form">
        <ContentHeader />
                <div className="sign-in-container">
                        <div className="container">
                            <div className="row si--title">
                                <div className="col-12">
                                    <img className="logo--text-only" src={logotextonly}  alt="logo"/>
                                </div>
                            </div>

                            <div className="row si--sub-title">
                                <div className="col-12 sub-title">
                                    <div className="text">  .</div>
                                    <div className="bar"></div>
                                </div>
                            </div>

                            <LoginForm />
                            <div className="row si--add-on find-id-password">
                                <div className="col-6 left">
                                    <p className="bar--description">
                                           ?
                                    </p>
                                </div>
                                <div className="col-6 right button-group">
                                    <Link to="/findid"><button type="button" className="btn btn-secondary btn-find-id"> </button></Link>
                                    <Link to="/findpasswd"><button type="button" className="btn btn-secondary btn-find-password"> </button></Link>
                                </div>
                            </div>

                            <div className="row si--add-on sign-up-membership">
                                <div className="col-6 left">
                                    <p className="bar--description">
                                          ?
                                    </p>
                                </div>
                                <div className="col-6 right button-group">
                                    <Link to="/signup">
                                    <button type="button" className="btn btn-secondary btn-sign-up">
                                        
                                        <img className="icon" src={userblueicon}  alt="register" />
                                    </button>
                                    </Link>
                                </div>
                            </div>
                        </div>
                    </div>
        
        </section>
        </div>
        </main>

        
    );
  }
}


export default withRouter(Login);


