const fakeAuth = {
  isAuthenticated: (sessionStorage.getItem('Auth')==null) ? false : true,
  authenticate(cb) {
    console.log(this.isAuthenticated);
    console.log("parameter" + cb['details']['remoteAddress']);
    this.isAuthenticated = cb['authenticated'];
    sessionStorage.setItem('Auth', JSON.stringify(cb));
    
  },
  signout(cb) {
    this.isAuthenticated = false;
    sessionStorage.clear();
    
  }
}

export default fakeAuth;


