import React, { Component } from 'react';


//import SidebarContent from './SidebarContent';
import ContentHeader from './ContentHeader';
import Product from './Product';
import Store from './Store';
import Settlement from './Settlement';
import Sidebar from './Sidebar';

/*const Sidebar = ()=>(
	<aside class="side-menu-container">
		<SidebarContent />
	</aside>
)

const MobileSidebar = ()=>(
	<aside class="side-menu-container for-mobile">
		<SidebarContent />
	</aside>
)*/
class Dashboard extends Component {

	constructor(props) {
		super(props);
		this.state = {
			 listDataFromChild: true,
			 screenName : props.match.params.screen
		};    
		
	}
	  
	myCallback = (dataFromChild) => {
		this.setState({ listDataFromChild: dataFromChild });
		
  	}

	componentWillMount(){
		
	}

	render(){
		console.log("State Screen : " + this.state.screen)
		console.log("Detail Screen : " + this.props.match.params.detail)
		const data = this.state.listDataFromChild;
		const screenName = (this.props.match.params.screen===undefined)?"main":this.props.match.params.screen;
		console.log("Dashboard : " + data);
		console.log("Screen Name : " + screenName);
		let screen = null;
		if(screenName=="main")screen = <MainScreen />
		else if(screenName=="benefit")screen = <Benefit />
		else if(screenName=="product")screen = <Product />
		else if(screenName=="store")screen = <Store />
		else if(screenName=="settlement")screen = <Settlement />
		else screen = <MainScreen />


		return(
			
			<main className={data ? "main-container":"main-container side-bar-flip" }>
			<div className="main-wrap">
			<Sidebar callbackFromParent={this.myCallback} activated={screenName}/>
			{screen}
			</div>
			</main>
			
		);
	}
}
const MainScreen = () => (
	<section className="section-container">
	<ContentHeader />
	<div class="section-top-container">
		<h1 class="st--title">
			Dashboard
		</h1>
		<h4 class="st--description">
			   ,       .
		</h4>
	</div>
	<div class="section-content-container">
		<div class="section-content-wrap">
		</div>
	</div>
	</section>

	
	)
	const Benefit = () => (
		<section className="section-container">
	<ContentHeader />
	<div class="section-top-container">
		<h1 class="st--title">
			 
		</h1>
		<h4 class="st--description">
			   ,       .
		</h4>
	</div>
	<div class="section-content-container">
		<div class="section-content-wrap">
		</div>
	</div>
	</section>
	)
	

export default Dashboard;


