import React, { Component } from 'react';
import {
  Link,
  withRouter
} from 'react-router-dom';
import PropTypes from 'prop-types';
import signupicon from './imgs/common/icon--sign-up.svg';
import findidicon from './imgs/common/icon--find-id.svg';
import findpasswordicon from './imgs/common/icon--find-password.svg';
import loginicon from './imgs/common/icon--login.svg';
import headerusericon from './imgs/common/icon--arrow--header-user.svg';
import notiicon from './imgs/common/icon--notification--header.svg';
import notiheadericon from './imgs/common/icon--notification--header--mark-new.svg';
import customlogo from './imgs/test/logo--nike.svg';
import PopupContainer from './PopupContainer';

class ContentHeader extends Component {

    static propTypes = {
        match: PropTypes.object.isRequired,
        location: PropTypes.object.isRequired,
        history: PropTypes.object.isRequired
    }

    constructor(props){
        super(props);
        
        this.state = {isToggleOn: false, user_auth:null};
        this.handleClick = this.handleClick.bind(this);

    }

    componentWillMount() {
        const { match, location, history } = this.props        
        console.log("App : " + location.pathname);
        this.setState({user_auth:JSON.parse(sessionStorage.getItem('Auth'))});

    }

    handleClick(e){
        e.preventDefault();
        
        this.setState(prevState => ({
            isToggleOn: !prevState.isToggleOn
        }));
        
        
    }
    

    render(){
        const { match, location, history } = this.props
        const { isToggleOn, user_auth } = this.state;
        console.log(location.pathname.split("/", 2)[1])
        


        if(user_auth!=null && location.pathname.split("/", 2)[1]=="dashboard"){
            console.log(user_auth['principal']['username']);
            return(
                
                <header class="header-container">
                <div class="header-wrap">
                    <div class="brand-container">
                        <div class="nc--brand-logo">
                            <img class="nc--bl--image" src={customlogo} />
                        </div>
 
                        <div class="nc--brand-name">
                            Nike Korea<span class="nc--bn--identity">(nike_korea)</span>
                        </div>
                    </div><div class="user-container">
                        <div class="nc--user-informations">
                            <button type="button" onClick={this.handleClick}>
                            <span class="nc--ui--message-front">! </span>
                            <span class="nc--ui--user">{user_auth['name']}</span>
                            <span class="nc--ui--message-back"></span>
                            </button>
                            <img class="icon--arrow" src={headerusericon} />
                            {isToggleOn ? <PopupContainer auth={user_auth}/> : null}
                            
                                               
                        </div>
 
                        <div class="user-notifications">
                            <img class="nc--un--icon" src={notiicon} />
 
                            <div class="nc--un--mark new">
                                <img src={notiheadericon} />
                            </div>
                        </div>
                    </div>
                </div>
            </header>
 
             );
                  
        }
        else {
            return(
        
                <header className="header-container">
                   <div className={location.pathname==="/" ? "header-wrap" : "header-wrap set-flex"  }>
                           {location.pathname==="/signup" && <div className="title-container">
                               <div className="title-text">
                                   <span className="tt--description">(Step1)</span>
                               </div>
                           </div>}
                           <div className="menu-container">
                               <div className="button-group">
                                       <Link to="/">
                                       <button type="button" className="btn btn-none btn-login">
                                           <img className="icon" src={loginicon} alt="login" />
                                           <span className="text"></span>
                                       </button>
                                       </Link>
                                       <Link to="/signup">
                                       <button type="button" className="btn btn-none btn-regiter">
                                           <img className="icon" src={signupicon} alt="signup" />
                                           <span className="text"></span>
                                       </button>
                                       </Link>
                                       <Link to="/findid">
                                       <button type="button" className="btn btn-none btn-find-id">
                                           <img className="icon" src={findidicon} alt="findid"/>
                                           <span className="text"> </span>
                                       </button>
                                       </Link>
                                       <Link to="/findpasswd">
                                       <button type="button" className="btn btn-none btn-find-password">
                                           <img className="icon" src={findpasswordicon} alt="findpassword"/>
                                           <span className="text"> </span>
                                       </button>
                                       </Link>
                               </div>
                           </div>
                   </div>
               </header>
                   );
            
 
        }
    }
}


export default withRouter(ContentHeader);



